<template>
    <v-container>
        <v-card-text>
              <v-container>
                  <v-row>
                    <v-col
                        cols="6"
                    >
                    <v-banner
                    elevation="9"
                    outlined
                    rounded
                    class="indigo darken-4 white--text"
                    >
                    <v-icon class="white">mdi-account-plus</v-icon>
                        Registro de Vecinos
                    </v-banner>
                    <v-spacer></v-spacer>
                    </v-col>
                  </v-row>
                  <v-form
                    ref="form"
                    v-model="valid"
                    @submit.prevent="guardarPersona">
                    <v-row>
                    <v-col
                        cols="12"
                        sm="6"
                        md="4"
                    >
                    <v-text-field
                    v-model="newItem.pNombre"
                    label="Primer Nombre"
                    :rules="Rules"
                    required
                    ></v-text-field>
                    </v-col>
                    <v-col
                        cols="12"
                        sm="6"
                        md="4"
                    >
                        <v-text-field
                        v-model="newItem.sNombre"
                        label="Segundo Nombre"
                        ></v-text-field>
                    </v-col>
                    <v-col
                        cols="12"
                        sm="6"
                        md="4"
                    >
                        <v-text-field
                        v-model="newItem.tNombre"
                        label="Tercer Nombre"
                        ></v-text-field>
                    </v-col>
                    <v-col
                        cols="12"
                        sm="6"
                        md="4"
                    >
                        <v-text-field
                        v-model="newItem.pApellido"
                        label="Primer Apellido"
                        :rules="Rules"
                        ></v-text-field>
                    </v-col>
                    <v-col
                        cols="12"
                        sm="6"
                        md="4"
                    >
                        <v-text-field
                        v-model="newItem.sApellido"
                        label="Segundo Apellido"
                        ></v-text-field>
                    </v-col>
                    <v-col
                        cols="12"
                        sm="6"
                        md="4"
                    >
                        <v-text-field
                        v-model="newItem.tApellido"
                        label="Apellido de Casada"
                        ></v-text-field>
                    </v-col>
                    <v-col
                        cols="12"
                        sm="6"
                        md="4"
                    >
                        <v-text-field
                        v-model="newItem.dpi"
                        label="Número de DPI"
                        type="number"
                        
                        ></v-text-field>
                    </v-col>
                    <v-col
                        cols="12"
                        sm="6"
                        md="4"
                    >
                        <v-text-field
                        v-model="newItem.nacimiento"
                        label="Fecha de nacimiento"
                        type="date"
                        
                        ></v-text-field>
                    </v-col>
                    <v-col
                        cols="12"
                        sm="6"
                        md="4"
                    >
                        <v-text-field
                        v-model="newItem.direccion"
                        label="Dirección"
                        :rules="Rules"
                        ></v-text-field>
                    </v-col>
                    <v-col
                        cols="12"
                        sm="6"
                        md="4"
                    >
                        <v-text-field
                        v-model="newItem.numero_casa"
                        label="No. Casa"
                       
                        ></v-text-field>
                    </v-col>
                    <v-col
                        cols="12"
                        sm="6"
                        md="4"
                    >
                    <v-select v-model="newItem.zona_id"
                        :items="zonas"
                        item-text="zona"
                        item-value='id'
                        label="Zona"
                        outlined
                        color="#10069f"
                        @change="getColonias()"
                        :rules="Rules"
                        ></v-select> 
                    </v-col>
                    <v-col
                        cols="12"
                        sm="6"
                        md="4"
                    >
                    <v-select v-model="newItem.colonia_id"
                        :items="colonias"
                        item-text="colonia"
                        item-value='id'
                        label="Colonia"
                        outlined
                        color="#10069f"
                        :rules="Rules"
                        ></v-select> 
                    
                    </v-col>
                    <v-col
                        cols="12"
                        sm="6"
                        md="4"
                    >
                    <v-text-field
                        v-model="newItem.telefono_casa"
                        label="Teléfono Residencial"
                        type="number"

                        ></v-text-field>
                        
                    </v-col>
                    <v-col
                        cols="12"
                        sm="6"
                        md="4"
                    >
                    <v-text-field
                        v-model="newItem.celular"
                        label="Celular"
                        type="number"
                        :rules="Rules"
                        ></v-text-field>
                    
                    </v-col>
                    <v-col
                        cols="12"
                        sm="6"
                        md="4"
                    >
                        <v-text-field
                        v-model="newItem.correo"
                        label="Correo Electrónico"
                        type="email"
                       
                        ></v-text-field>
                        
                    </v-col>

                    <v-col
                        cols="12"
                        sm="6"
                        md="4"
                    >
                    <v-select v-model="newItem.genero"
                            :items="['M', 'F']"
                            label="Género"
                            outlined
                            color="#10069f"
                            :rules="Rules"
                        ></v-select>
                        
                    </v-col>
                    <v-col
                        cols="12"
                        sm="6"
                        md="4"
                    >
                        <v-select v-model="newItem.liderazgo"
                        :items="liderazgo"
                        item-text="liderazgo"
                        item-value='liderazgo'
                        label="Liderazgo comunitario"
                        outlined
                        color="#10069f"
                        :rules="Rules"
                        ></v-select>
                    </v-col>
                    <v-col
                        cols="12"
                        sm="6"
                        md="4"
                    >
                        <v-select v-model="newItem.seguimiento"
                        :items="tipoVecino"
                        item-text="text"
                        item-value='value'
                        label="Vecino de tipo"
                        outlined
                        color="#10069f"
                        :rules="Rules"
                        ></v-select>
                    </v-col>
                    </v-row>
                    <v-row>
                        <v-card>
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn
                                    color="blue darken-1"
                                    text
                                    type="submit"
                                >
                                    Guardar
                                </v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-row>
                  </v-form>
              </v-container>
            </v-card-text>

    </v-container>
</template>
<script>
import axios from 'axios'

export default ({
    mounted() {
        this.getZonas()
        this.getLiderazgo()
    },

    data:()=>({ 
          colonias:[],
          valid: true,
          tipoVecino:[
              {
                  text:'Satisfecho',
                  value:1
              },
              {
                  text:'Muy Satisfecho',
                  value:3
              }

          ],
          zonas:[],
          newItem:{
            "pNombre": "",
            "sNombre": "",
            "tNombre": "",
            "pApellido": "",
            "sApellido": "",
            "tApellido": "",
            "dpi": null,
            "nacimiento": "",
            "direccion": "",
            "numero_casa": "",
            "zona_id": "",
            "colonia_id": "",
            "telefono_casa": null,
            "celular":"",
            "correo": null,
            "genero": "",
            "liderazgo": "",
            "seguimiento":"", //Seguimiento 1 corresponde a los vecinos satisfechos.
            "usuario_creador": JSON.parse(localStorage.getItem('username')),
          },
          liderazgo:[],
          Rules: [
                v => !!v || 'Campo requerido',
            ],
            emailRules: [
        v => /.+@.+/.test(v) || 'E-mail debe ser válido',
      ],
    }),
    methods: {
        getZonas(){
            axios.get(process.env.VUE_APP_SERVICE_URL+'catalogo/zonaalcaldia/'+JSON.parse(localStorage.getItem('alcaldia')))
                .then((response)=>{
                    this.zonas=response.data.zona;
                })
                .catch(function(error){
                   console.log(error.response.data);
                })

        },
        getColonias(){
            axios.get(process.env.VUE_APP_SERVICE_URL+'catalogo/coloniasporzona/'+this.newItem.zona_id)
                .then((response)=>{
                    this.colonias=response.data.colonias;
                  
                })
                .catch(function(error){
                   console.log(error.response.data);
                })

        }, 
        getLiderazgo(){
            axios.get(process.env.VUE_APP_SERVICE_URL+'catalogo/liderazgo')
                .then((response)=>{
                    this.liderazgo=response.data.liderazgo
                   
                })
                .catch(function(error){
                   console.log(error.response.data);
                })

        },
        guardarPersona(){
            this.$refs.form.validate()
            if(this.valid){
                axios.post(process.env.VUE_APP_SERVICE_URL+'persona/register', this.newItem)
                .then(
                    this.$swal({
                        title:"Registrar Vecino",
                        text:"Vecino registrado con éxito",
                        icon:"success"
                    }).then((result)=>{
                        if(result.isConfirmed){
                            this.limpiarNuevo();

                        }
                    })
                )
                .catch(function(error){
                console.log(error.response.data)
                })
            }
   
        
        },
        limpiarNuevo(){
           this.newItem={
            "pNombre": "",
            "sNombre": "",
            "tNombre": "",
            "pApellido": "",
            "sApellido": "",
            "tApellido": "",
            "dpi": null,
            "nacimiento": "",
            "direccion": "",
            "numero_casa": "",
            "zona": "",
            "telefono_casa": null,
            "celular":"",
            "correo": null,
            "genero": "",
            "liderazgo": "",
            "seguimiento":"1",
            "usuario_creador": JSON.parse(localStorage.getItem('username')),
          }
    },
}
})
</script>
